import React, { Component } from 'react';
import firebase from 'firebase/app';
import 'firebase/auth';

class FlatmatesList extends Component {
    state = {}

    componentDidMount() {
        const auth = firebase.auth();
        const db = firebase.firestore();
        const container = document.querySelector(".container");

        auth.onAuthStateChanged(user => {
            if (user) {
                // Get data 
                db.collection('users').get().then(snapshot => {
                    setupBoard(snapshot.docs)
                });
                container.style.display = "block";
            } else {
                setupBoard([]);
                container.style.display = "none";
            }
        });

        const boardList = document.querySelector(".boardList");

        // Setup board
        const setupBoard = (data) => {

            let html = '';
            data.forEach(doc => {
                const board = doc.data();
                const tr = `
                <tr>
                    <td>${board.name}</td>
                    <td>${board.email}</td>
                    <td>${board.score}</td>
                </tr>
                `;
                html += tr
            });
            boardList.innerHTML = html;
        }
    }
    render() {
        return (
            <div className="container">
                <p>Here is the board with the list of the flatemates and their score</p>
                <br />
                <table>
                    <thead>
                        <tr>
                            <th>Name</th>
                            <th>Email</th>
                            <th>Score</th>
                        </tr>
                    </thead>

                    <tbody className="boardList">

                    </tbody>
                </table>
            </div>
        );
    }
}

export default FlatmatesList;